import React, { useState } from 'react'
import $ from 'jquery';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';

import verifiedLogo from './imgs/verified-24px.svg';
import logo from './logo.svg';
// Components
import Portfolio from './Portfolio.js';
import AboutMe from './AboutMe.js';
import Resume from './Resume.js';

// Portfolio Pages
import SenjuPrac from './components/portfolio-items/SenjuPrac.js';
import PlantsVsRobots from './components/portfolio-items/PlantsVsRobots.js';
import StickyTongue from './components/portfolio-items/StickyTongue.js';

export default function ContainerPage() {
    const [page, setPage] = useState(0);

    const projectsLink = document.getElementById('projectsLink');
    const aboutMeLink = document.getElementById('aboutMeLink');
    const resumeLink = document.getElementById('resumeLink');

    function clickProjects(e) {
        // e.preventDefault();
        if (page != 0) {
            setPage(0);
            $('#projectsLink').addClass('activeBtn');
            $('#aboutMeLink').addClass('inactiveBtn');
            $('#resumeLink').addClass('inactiveBtn');
            $('#aboutMeLink').removeClass('activeBtn');
            $('#resumeLink').removeClass('activeBtn');
        }
    }

    function clickAboutMe(e) {
        // e.preventDefault();
        if (page != 1) {
            setPage(1);
            $('#aboutMeLink').addClass('activeBtn');
            $('#projectsLink').addClass('inactiveBtn');
            $('#resumeLink').addClass('inactiveBtn');
            $('#projectsLink').removeClass('activeBtn');
            $('#resumeLink').removeClass('activeBtn');
        }

    }

    function clickResume(e) {
        // e.preventDefault();
        if (page != 2) {
            setPage(2);
            $('#resumeLink').addClass('activeBtn');
            $('#projectsLink').addClass('inactiveBtn');
            $('#aboutMeLink').addClass('inactiveBtn');
            $('#projectsLink').removeClass('activeBtn');
            $('#aboutMeLink').removeClass('activeBtn');
        }
    }

    function clicked(e) {
        e.preventDefault()
        console.log(page);
    }

    return (
        <Router>
        <div id="containerDiv">
            <div id="header">
                <div id="header-gradient"></div>
                <h6 id="verified"><img src={verifiedLogo} /> Verified</h6>
                <h1 id="header-name">Alan Park</h1>
                <div id="header-btns">
                    {/* <button className="overviewBtns activeBtn" id="projectsLink" onClick={clickProjects}>Projects</button> */}
                    <Link to="/" className="overviewBtns activeBtn" id="projectsLink" onClick={clickProjects}>Projects</Link>
                    {/* <button className="overviewBtns inactiveBtn" id="aboutMeLink" onClick={clickAboutMe}>About</button> */}
                    <Link to="/AboutMe" className="overviewBtns inactiveBtn" id="aboutMeLink" onClick={clickAboutMe}>About</Link>
                    {/* <button className="overviewBtns inactiveBtn" id="resumeLink" onClick={clickResume}>Resume</button> */}
                    <Link to="/Resume" className="overviewBtns inactiveBtn" id="resumeLink" onClick={clickResume}>Resume</Link>
                    {/* <button id="clickme" onClick={clicked}>Click me</button> */}
                </div>

            </div>



            <div className="container-fluid containersBody">
                <Route path='/' exact component={Portfolio} />
                <Route path='/AboutMe' component={AboutMe} />
                <Route path='/Resume' component={Resume} />
                {/* <Route path='/PortfolioPage' component={PortfolioPage} /> */}
                <Route path='/projects/SenjuPrac' component={SenjuPrac} />
                <Route path='/projects/PlantsVsRobots' component={PlantsVsRobots} />
                <Route path='/projects/StickyTongue' component={StickyTongue} />
            </div>



            <footer id="footer">
                <div className="container">
                <div className="row">
                    <div className="col-md-6">
                    <h5>Alan Park</h5>
                    <p>Santa Clara, CA</p>
                    <p>alansdpark@gmail.com</p>
                    <br />
                    <div className="row">
                        <div className="col-2"><a href="https://github.com/alansdpark" target="_blank" className="social-links"><i className="fab fa-github fa-2x"></i></a></div>
                        <div className="col-2"><a href="https://www.linkedin.com/in/alan-p-568987139/" target="_blank" className="social-links"><i className="fab fa-linkedin fa-2x"></i></a></div>
                    </div>
                    </div>
                    <div className="col-md-6">
                    <p>Made with React<img id="react-logo" src={logo} /></p>
                    <p>IN DEVELOPMENT -- Please excuse any errors</p>
                    <p>Last Updated: Oct 2, 2020</p>
                    </div>
                </div>
                </div>
            </footer>
        </div>
        </Router>

    )
}
